import React from 'react';

import './App.scss';
import { Route, Switch, BrowserRouter } from 'react-router-dom';

const App = () => (
    <div className="App">
      <h1 className="App__title">Info-collapse</h1>
      <BrowserRouter basename={process.env.PUBLIC_URL}>
        <Switch>
          <Route
            path="/people"
          >
            <p>People</p>
          </Route>
    
          <Route
            path="/"
            exact
          >
            <p>Home</p>
          </Route>
        </Switch>
      </BrowserRouter>
    </div>
  );

export default App;
